<template>
  <div class="question-new">
    <el-card class="box-card">
      <div slot="header">{{ id ? '试题修改' : '试题录入' }}</div>

      <!-- 表单 -->
      <el-form
        ref="form"
        :model="form"
        :rules="rules"
        label-width="120px"
        class="demo-ruleForm"
      >
        <el-form-item label="学科：" prop="subjectID">
          <el-select
            v-model="form.subjectID"
            placeholder="请选择"
            style="width: 400px"
            @change="handleSubjectChange"
          >
            <el-option
              v-for="item in subjectList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="目录：" prop="catalogID">
          <el-select
            v-model="form.catalogID"
            placeholder="请选择"
            style="width: 400px"
          >
            <el-option
              v-for="item in directoryList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="企业：" prop="enterpriseID">
          <el-select
            v-model="form.enterpriseID"
            placeholder="请选择"
            style="width: 400px"
          >
            <el-option
              v-for="item in companyList"
              :key="item.id"
              :label="item.company"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="城市：" prop="city">
          <el-select
            v-model="form.province"
            placeholder="请选择"
            style="width: 198px"
            @change="handleProvinceChange"
          >
            <el-option
              v-for="(item, i) in provinceList"
              :key="i"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
          <el-select
            v-model="form.city"
            placeholder="请选择："
            style="width: 198px; margin-left: 4px"
          >
            <el-option
              v-for="(item, i) in cityList"
              :key="i"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="方向：" prop="direction">
          <el-select
            v-model="form.direction"
            placeholder="请选择"
            style="width: 400px"
          >
            <el-option
              v-for="(item, i) in direction"
              :key="i"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="题型：" prop="questionType">
          <el-radio-group v-model="form.questionType">
            <el-radio
              v-for="(el, i) in questionType"
              :key="i"
              :label="el.value"
              >{{ el.label }}</el-radio
            >
          </el-radio-group>
        </el-form-item>

        <el-form-item label="难度：" prop="difficulty">
          <el-radio-group v-model="form.difficulty">
            <el-radio
              v-for="(el, i) in difficulty"
              :key="i"
              :label="el.value + ''"
              >{{ el.label }}</el-radio
            >
          </el-radio-group>
        </el-form-item>

        <el-form-item label="题干：" prop="question">
          <quill-editor
            ref="editor1"
            class="quill-editor"
            v-model="form.question"
            :options="editorOption"
          >
          </quill-editor>
        </el-form-item>

        <!-- 三种情况 -->
        <!-- 1. 题型为单选时为单选框 -->
        <!-- 2. 题型为多选时为多选框 -->
        <!-- 3. 题型为简答时隐藏 -->
        <el-form-item
          v-if="form.questionType != 3"
          label="选项："
          class="options"
        >
          <div v-for="(el, i) in form.options" :key="i" class="options-item">
            <!-- 单选 -->
            <template v-if="form.questionType == 1">
              <el-radio v-model="singleChoice" :label="i">{{
                el.code + '：'
              }}</el-radio>
            </template>

            <!-- 多选 -->
            <template v-if="form.questionType == 2">
              <el-checkbox
                v-model="el.isRight"
                :checked="Boolean(el.isRight)"
                :true-label="1"
                :false-label="0"
                >{{ el.code + '：' }}</el-checkbox
              >
            </template>

            <el-input v-model="el.title"></el-input>
            <el-upload action="/">
              上传图片
              <i class="el-icon-circle-close"></i>
            </el-upload>
          </div>

          <!-- 添加选项 -->
          <el-button
            size="small"
            :disabled="form.questionType == 1"
            type="danger"
            @click="addOption"
            >+增加选项与答案</el-button
          >
        </el-form-item>

        <el-form-item label="解析视频：">
          <el-input v-model="form.videoURL" style="width: 400px" />
        </el-form-item>

        <el-form-item label="答案解析：" prop="answer">
          <quill-editor
            ref="editor2"
            class="quill-editor"
            v-model="form.answer"
            :options="editorOption"
          ></quill-editor>
        </el-form-item>

        <el-form-item label="题目备注：">
          <el-input
            v-model="form.remarks"
            type="textarea"
            style="width: 400px"
            rows="4"
          />
        </el-form-item>

        <el-form-item label="试题标签：">
          <el-select
            multiple
            v-model="form.tags"
            placeholder="请选择试题标签"
            style="width: 400px"
          >
            <el-option
              v-for="(item, i) in tagList"
              :key="i"
              :value="item"
              :label="item"
            >
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="submitForm">{{
            id ? '确认修改' : '确认提交'
          }}</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor } from 'vue-quill-editor'
import { list as getCompanyList } from '@/api/hmmm/companys.js'
import { simple as getSubjectList } from '@/api/hmmm/subjects.js'
import { simple as getDirectoryList } from '@/api/hmmm/directorys.js'
import { simple as getTagList } from '@/api/hmmm/tags.js'
import { provinces, citys } from '@/api/hmmm/citys.js'
import { direction, questionType, difficulty } from '@/api/hmmm/constants.js'
import { add as addQuestions, detail, update } from '@/api/hmmm/questions.js'
export default {
  components: { quillEditor },
  data() {
    return {
      id: this.$route.query.id,
      editorOption: {
        modules: {
          toolbar: [
            ['bold', 'italic', 'underline', 'strike'],
            [{ list: 'ordered' }, { list: 'bullet' }],
            ['blockquote'],
            ['code-block', 'image', 'link']
          ]
        },
        placeholder: ''
      },
      companyList: [],
      subjectList: [],
      directoryList: [],
      tagList: [],
      provinceList: [],
      cityList: [],
      questionType,
      direction: direction.slice(0),
      difficulty,
      form: {
        questionType: 1,
        difficulty: '1',
        options: [
          { isRight: 0, code: 'A', title: '' },
          { isRight: 0, code: 'B', title: '' },
          { isRight: 0, code: 'C', title: '' },
          { isRight: 0, code: 'D', title: '' }
        ]
      },
      singleChoice: null,
      multipleChoice: [],
      rules: {
        subjectID: [{ required: true, message: '请选择学科' }],
        catalogID: [{ required: true, message: '请选择目录' }],
        enterpriseID: [{ required: true, message: '请选择企业' }],
        direction: [{ required: true, message: '请选择方向' }],
        questionType: [{ required: true, message: '请选择题型' }],
        difficulty: [{ required: true, message: '请选择难度' }],
        question: [{ required: true, message: '请输入解析' }],
        answer: [{ required: true, message: '请输入答案' }],
        city: [{ required: true, message: '请选择地区', trigger: 'change' }]
      }
    }
  },
  async created() {
    this.getCompanyList()
    this.getSubjectList()
    this.provinceList = provinces()
    if (this.id) this.initForm()
  },
  mounted() {
    if (this.id) {
      this.$refs.editor1.quill.enable(false)
      this.$refs.editor2.quill.enable(false)
    }
  },
  methods: {
    async initForm() {
      try {
        const { data } = await detail({ id: this.id })
        this.form = data
        this.form.questionType = Number(this.form.questionType)
        if (this.form.questionType === 1) {
          this.singleChoice = this.form.options.findIndex(el => el.isRight)
        }
        this.getDirectoryList(this.form.subjectID)
        this.cityList = citys(this.form.province)
        this.getTagList(this.form.subjectID)
        if (typeof this.form.tags === 'string') {
          this.form.tags = this.form.tags.split(',')
        } else if (!this.form.tags) this.form.tags = []
        this.$nextTick(() => {
          this.$refs.editor1.quill.enable(true)
          this.$refs.editor2.quill.enable(true)
        })
      } catch (err) {
        console.log(err)
      }
    },
    submitForm() {
      this.$refs.form.validate(valid => {
        if (valid) {
          if (Number(this.form.questionType) === 1) {
            if (this.singleChoice && this.singleChoice !== -1) {
              this.form.options[this.singleChoice].isRight = 1
            }
          } else {
            this.form.options = []
          }
          Array.isArray(this.form.tags) &&
            (this.form.tags = this.form.tags.join())
          this.form.questionType += ''
          this.form.difficulty += ''
          this.addQuestions()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    async addQuestions() {
      try {
        if (this.id) {
          await update(this.form)
        } else {
          await addQuestions(this.form)
        }
        const message = this.id ? '修改成功' : '添加成功'
        this.$message({
          message,
          type: 'success'
        })
        this.$router.push('/questions/list')
      } catch (err) {
        console.log(err)
      }
    },
    addOption() {
      const options = this.form.options
      const charCode = options[options.length - 1].code.charCodeAt(0)
      options.push({
        isRight: 0,
        code: String.fromCharCode(charCode + 1)
      })
    },
    async getCompanyList() {
      try {
        const { data } = await getCompanyList({ pagesize: 10000 })
        this.companyList = data.items
      } catch (err) {
        console.dir(err)
      }
    },
    async getSubjectList() {
      try {
        const { data } = await getSubjectList()
        this.subjectList = data
      } catch (err) {
        console.log(err)
      }
    },
    handleSubjectChange(id) {
      // 获取子目录和标签
      delete this.form.catalogID
      this.getDirectoryList(id)
      this.getTagList(id)
    },
    async getDirectoryList(id) {
      try {
        const { data } = await getDirectoryList({ subjectID: id })
        this.directoryList = data
      } catch (err) {
        console.log(err)
      }
    },
    async getTagList(id) {
      try {
        const { data } = await getTagList({ subjectID: id })
        data.forEach(el => {
          this.tagList.push(el.label)
        })
      } catch (err) {
        console.log(err)
      }
    },
    handleProvinceChange(val) {
      this.cityList = citys(val)
      this.$refs.form.validateField('city')
    }
  }
}
</script>

<style scoped lang="scss">
.question-new {
  padding: 20px;
  ::v-deep .quill-editor {
    .ql-toolbar {
      padding: 0 8px;
    }
    .ql-container {
      height: 200px;
    }
  }
  ::v-deep .options-item {
    display: flex;
    align-items: center;
    padding-bottom: 20px;
    .el-radio {
      margin-right: 0;
    }
    .el-input {
      width: 240px;
    }
    .el-upload {
      width: 100px;
      height: 60px;
      margin-left: 4px;
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      line-height: 60px;
      text-align: center;
      .el-icon-circle-close {
        position: absolute;
        right: 0;
        top: 0;
        transform: translate(50%, -50%);
        background: #fff;
        font-size: 18px;
        color: #999;
      }
    }
    .el-upload:hover {
      border-color: #409eff;
    }
  }
}
</style>
